.suggestions {
  border-radius: 8px;
  background: #ffffff;
  margin-bottom: 30px;
}
.suggestions-header {
  background: #f2f2f2;
  padding: 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.suggestion-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  overflow: hidden;
}
.suggestion-image {
  width: 100%;
  height: 100%;
}
.single-suggestion {
  display: flex;
  align-content: center;
  padding: 1em;
  justify-content: space-between;
}
.single-suggestion .btn {
  font-size: 12px;
  padding: 0.7em;
}
.suggested-name {
  margin: auto 0;
}
.tagline {
  padding: 1em;
  padding-bottom: 0;
  margin-bottom: 0.3em;
}
.dashed-border-bottom {
  border-bottom: 1px dashed #c4c4c4;
}
.suggestion-footer {
  padding: 1em 1em;
  padding-top: 0.1em;
}
.suggestion-footer-link {
  color: #00b0f0;
  font-size: 16px;
}
.suggestion-footer-link:hover {
  cursor: pointer;
  text-decoration: none;
  color: #333333;
}

.trending-section-header {
  padding: 1em;
}
.trending-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-right: 1em;
  overflow: hidden;
}
.trending-image {
  width: 100%;
  height: 100%;
}
.trending-name p {
  margin-bottom: 0;
  font-size: 16px;
}
.trending-name {
  margin: auto 0;
}

.trending-section-content {
  padding: 1em;
  border-radius: 8px;
  background: #ffffff;
}

.trending-text p {
  font-size: 14px;
  text-align: justify;
  margin-top: 0.5em;
}
.like-btn {
  background: transparent;
  border: none;
  padding: 0;
}
.like-btn:hover {
  cursor: pointer;
  color: #00b0f0;
}
.trending-post {
  padding: 1em 0;
}

.sidebar {
  width: 270px;
  margin-right: 1em;
}
.sidebar p {
  font-size: 14px;
}
.sidebar a,
.sidebar-section a,
.sidebar-section a:hover {
  color: #333;
  text-decoration: none !important;
}
.sidebar-top {
  background: #ffffff;
  border-radius: 4px;
  padding-bottom: 2em;
  margin-bottom: 1em;
}
.sidebar-footer {
  background: #ffffff;
  border-radius: 4px;
  padding: 1em;
}
.sidebar-cover-image-wrapper {
  width: 100%;
  height: 130px;
  border-radius: 4px;
  overflow: hidden;
}
.sidebar-cover-image {
  width: 100%;
  min-height: 200px;
  height: auto;
}
.sidebar-section {
  margin: 0 1em;
  border-bottom: 1px dashed #c4c4c4;
  padding: 1em;
  border-top: 0px solid white;
}
.sidebar-section.\--lift-up {
  margin-top: -60px;
}
.sidebar-section.\--no-border {
  border: none;
}
.sidebar-profile-image-wrapper {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin: auto;
  margin-bottom: 1em;
  overflow: hidden;
}
.sidebar-profile-image,
.politician-profile-image {
  width: 100%;
  height: 100%;
}
.sidebar-section p {
  margin-bottom: 0.2em;
}
.sidebar-section .text-gclout-blue {
  margin-bottom: 0.5em;
}
.section-half {
  width: 50%;
}

.politician-profile-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: auto;
  overflow: hidden;
}
.sidebar-politician {
  margin: 1em 0;
}
.sidebar-footer > p > a {
  color: #333333;
}
.sidebar-footer > p > a:hover {
  color: #00b0f0;
  text-decoration: none;
}
.sidebar-footer .smaller-margin {
  margin-bottom: 0.2em;
}
.advert-section {
  padding-top: 1em;
}
.advert-section p {
  font-size: 16px;
}
.advert-link {
  color: #333333;
}
.advert-link:hover {
  color: #00b0f0;
  text-decoration: none;
}
.friends-details {
  padding-top: 0.6em;
}
@media screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }
}

.make-post {
  border-radius: 8px;
  background: #ffffff;
  flex: 1 1;
  align-self: flex-start;
  margin-bottom: 1em;
}
.make-post.not-rounded > .make-post-header,
.make-post.not-rounded {
  border-radius: 0 !important;
}
.make-post-header {
  background: #f2f2f2;
  padding: 0.5em 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.btn-tab {
  border: none;
  margin: 0 !important;
  background: transparent;
  border-right: 1px solid #c4c4c4;
  border-radius: 0;
}
.btn-tab:focus,
.btn-tab:active {
  box-shadow: 0 0 0 0;
  border: none;
  outline: none;
  border-right: 1px solid #c4c4c4;
}
.new-post-container {
  max-height: 0px;
  overflow: hidden;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.new-post-container.show {
  max-height: 1000px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.close-btn {
  font-size: 21px;
  padding-bottom: 0;
  margin-bottom: 0;
}
.close-btn:hover {
  cursor: pointer;
  color: #00b0f0;
}
.dropzone {
  position: relative;
  width: 120px;
  height: 120px;
  border: 2px dashed #00b0f0;
  border-radius: 5px;
  color: #00b0f0;
  padding: 30px 0;
  text-align: center;
}
.dropzone:hover {
  cursor: pointer;
}
.image-preview-wrapper {
  width: 120px;
  height: 120px;
  border-radius: 5px;
  margin: 0 1em;
  overflow: hidden;
}
.image-preview-wrapper > img {
  width: auto;
  height: 100%;
}
.preview-delete {
  background: #f00048;
  color: #ffffff;
  text-align: center;
  padding: 1em;
  border: none;
  border-radius: 4px;
  font-size: 11px;
  margin: auto;
}
.preview-delete:hover {
  cursor: pointer;
}
.preview-overlay {
  background: rgba(0, 0, 0, 0.4);
  width: 120px;
  height: 120px;
  position: absolute;
  z-index: 11;
  display: none;
  align-content: center;
  justify-content: center;
  margin-top: -120px;
  border-radius: 5px;
}
.image-preview-wrapper:hover > .preview-overlay {
  display: flex;
}
.button-dropdown {
  display: none;
  left: -100px;
  right: 0;
  z-index: 9999999;
  background: #ffffff;
  color: #333333;
  border-radius: 4px;
  box-shadow: 1px 2px 1px 0px rgba(0,0,0,0.3);
}
.button-dropdown.show {
  display: block;
}
.button-dropdown-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.button-dropdown-list-button {
  background: transparent;
  padding: 0.25em 7em 0.25em 0;
  margin: 0;
  text-align: left;
  border: none;
}
.button-dropdown-list-button:hover {
  background-color: #00b0f0;
  color: #ffffff;
  text-align: left;
}
/*[data-placement] {
  left: -175px !important;
}*/
.btn-white {
  margin: 0;
  background: #ffffff;
}

.post-link,
.post-link:hover,
.post-link:focus,
.post-link:visited,
.post-link:active {
  color: #333333;
  text-decoration: none;
}
.post-container {
  background: #ffffff;
  padding: 1em 2em;
}
.post-container:hover {
  background: #fdfdfd;
}
.post-owner {
  display: flex;
  align-content: center;
}
.post-owner-image-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 100px;
  overflow: hidden;
  margin-right: 1em;
}
.post-owner-image {
  width: 100%;
  height: 100%;
}
.post-owner-details > p {
  font-weight: 600;
  margin: auto 0;
}
.post-owner-details {
  align-self: center;
}
.post-type {
  opacity: 0.7;
  font-weight: 400 !important;
}
.post-content {
  padding: 1em 0 0 0;
}
.post-content > p {
  text-align: justify;
}
.post-actions-container {
  padding: 1em 2em;
  background: #f1f1f1;
  display: flex;
  justify-content: space-between;
}
.post-action {
  background: transparent;
  border: none;
  line-height: 24px;
  font-size: 19px;
}
.flipped-reply {
  -webkit-transform: matrix(-1, 0, 0, 1, 0, 0);
          transform: matrix(-1, 0, 0, 1, 0, 0);
}
.post-action:hover, .post-action.active {
  cursor: pointer;
  color: #00b0f0;
}
.post-media-container {
  width: 100%;
}
.post-image {
  width: 100% !important;
}
.petition-header {
  background: #ffffff;
  border-bottom: 1px solid #cfcfcf;
  display: flex;
  justify-content: space-between;
  padding: 0.5em 1em;
}
.petition-header p , .petition-header a{
  margin-bottom: 0;
  padding-bottom: 0;
  text-decoration: none;
  color: #333333;
}
.styled-text {
  margin-bottom: 0;
  padding: 0;
  align-self: center;
}
.post-actions-container .btn {
  margin: 0;
}
.dropup {
  padding: 1em 3em;
  border-radius: 8px;
  display: none;
  left: -10em;
  margin-top: -10px
}
.dropup.show {
  display: block;
}
.dropup-list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-content: center;
}
.dropup-list-item button {
  border: none;
  margin: 4px;
  background: #00b0f0;
  border-radius: 50px;
  padding: 0.5em;
  box-shadow: 2px 2px 8px rgba(0,0 ,0,0.2);
}
.dropup-list-item button:hover {
  box-shadow: 4px 4px 8px rgba(0,0 ,0,0.2);
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
}
.dropup-list-item a{
  padding: 0.9em;
  font-size: 14px;
  color: #ffffff;
  border-radius: 50%;
}
